import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
{
    path: '/',
    name: 'datadisplay',
    redirect: '/datadisplay/incomeanalysis',
    component: () => import('../views/DataDisplay/DataDisplay.vue'),
    children:[
      {
        path: '/datadisplay/incomeanalysis',
        name: 'incomeanalysis',
        component: () => import('../views/DataDisplay/components/IncomeAnalysis.vue'),
      },
      {
        path: '/datadisplay/berthdata',
        name: 'berthdata',
        component: () => import('../views/DataDisplay/components/BerthData.vue'),
      },
      {
        path: '/datadisplay/vehicledata',
        name: 'vehicledata',
        component: () => import('../views/DataDisplay/components/VehicleData.vue'),
      },
      {
        path: '/datadisplay/devicedata',
        name: 'devicedata',
        component: () => import('../views/DataDisplay/components/DeviceData.vue'),
      },
    ]
},]

const router = new VueRouter({
    routes
})

export default router